import React from 'react'
import { Button, Modal, Input, Icon, Upload } from 'antd'
import Common from '/page/common.jsx'
const ButtonGroup = Button.Group

class Case extends Common{

	async componentWillMount () {
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '项目编号',
			dataIndex: 't1'
		},{
			title: '合同名称',
			dataIndex: 't2'
		},{
			title: '客户名称',
			dataIndex: 't3'
		},{
			title: '合同额',
			dataIndex: 't4'
		},{
			title: '合同起始日期',
			dataIndex: 't5'
		}],
		modalConf: {visible: false, item: {}},
		picmodelConf: {visible: false, item: {}}
	})

	search = async _ => {
		let data = [{key: 1, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 2, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 3, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 4, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 5, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 6, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 7, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'},
		{key: 8, t1: 'BDG8763', t2: '移动运维三期', t3: '中国移动', t4: '60000', t5: '2019年2月1日'}]
		this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: 8,
					current: 0
				})
			})
	}

	delete = _ => {
		this.handleOk(deletesupplier, 'id', '删除')
	}

	done = async _ => {
		let modalConf = {visible: false, item: {}}
		await this.setState({ modalConf })
		this.research()
	}

	openPicModel = _ => {
		this.setState({picmodelConf: {visible: true}})
	}

	excelImport = _ => {
		this.setState({visible: true})
	}

	renderSearch = _ => <div>
		<Input 
		value={this.state.search.name}
		suffix={this.state.search.name ? <Icon 
		type="close-circle" 
		onClick={_ => this.changeSearch({name: ''})} /> : null}
		onChange={e => this.changeSearch({name: e.target.value})}
		style={{width: 250}}
		addonBefore="名称" placeholder="名称" />
		<ButtonGroup>
		<Button 
		onClick={this.search}
		type="primary" icon="search">搜索</Button>
		<Button 
		onClick={this.research}
		type="primary" icon="retweet">重置</Button>
		<Button 
		onClick={_ => this.addmodal('modalConf', '添加')}
		type="primary" icon="plus">添加</Button>
		<Button 
		onClick={_ => this.editmodal('modalConf', '编辑')}
		type="primary" icon="edit">修改</Button>
		<Button 
		onClick={this.changemodel}
		type="primary" icon="delete">删除</Button>
		<Button 
		onClick={this.openPicModel}
		type="primary" icon="picture">影像</Button>
		<Button 
		onClick={this.excelImport}
		type="primary" icon="file-excel">EXCEL导入</Button>
		</ButtonGroup>
	</div>

	rendermodal = _ => [
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="上传"
		  cancelText="取消"
		>
			<Upload>
    <Button>
      <Icon type="upload" /> excel导入
    </Button>
  </Upload>
		</Modal>]

}

export default Case


